declare var $: any
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HttpServicesProvider } from '../../../../providers/http-services';
import { Utils } from '../../../../providers/Utils';
import { StorageProvider } from '../../../../providers/storage';
import { PhoneDeviceProvider } from '../../../../providers/phone-device';
/**
 * Generated class for the LmtagfiletoolPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lmtagfiletool',
  templateUrl: 'lmtagfiletool.html',
})
export class LmtagfiletoolPage {

  public toollist = [];
  public id = '';

  constructor(public navCtrl: NavController, public storage: StorageProvider, public navParams: NavParams, public httpService: HttpServicesProvider,
    public phoneDevice: PhoneDeviceProvider) {
    this.phoneDevice.phoneBacktoPop(this.navCtrl);
    // this.projectid = this.storage.get(StorageProvider.LM_PROJECTID);
    this.id = this.navParams.get('id')
  }
  ionViewWillEnter() {//当将要进入页面时触发
    this.toollist = [];
    // this.initlist(1);
    this.initTool();
  }

  initTool() {
    var url = "/lmtagFileTooldetail/list?standardcode=" + this.id + "&tooltype=tool&sort=seqnum&order=asc&_search=false&rows=-1&page=1&sidx=&sord=asc"

    this.httpService.get(url, null, (result) => {
      if (result.rows.length > 0) {
        this.toollist = result.rows;
      }
    });
  }

  doRefresh(refresher) {//下拉刷新

    this.initTool()
    setTimeout(() => {
      refresher.complete(); //停止下拉刷新
    }, 2000)
  }
  doInfinite(infiniteScroll) {//上拉加载
    this.initTool()
    infiniteScroll.complete();//停止上拉加载
    // infiniteScroll.enable(false);//停止上拉加载
  }

}



